import React, {useState, useEffect} from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import SwipeableViews from 'react-swipeable-views';
import { autoPlay } from 'react-swipeable-views-utils';
import { API } from "../../config";
import { sliderImages } from './../apiCore';

const AutoPlaySwipeableViews = autoPlay(SwipeableViews);


const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: '100%',
        flexGrow: 1,
    },
    header: {
        display: 'flex',
        alignItems: 'center',
        height: 50,
        paddingLeft: theme.spacing(4),
        backgroundColor: theme.palette.background.default,
    },
    img: {
        height: '100%',
        display: 'block',
        maxWidth: '100%',
        overflow: 'hidden',
        width: '100%',
    },
}));

function SwipeableTextMobileStepper() {
    const classes = useStyles();
    const theme = useTheme();
    const [slides, setSlides] = useState(null)
    const [activeStep, setActiveStep] = React.useState(0);


    // React.useEffect(() => {
    //   props.slides && props.slides.length && setSlides(props.slides.length);
    // }, [props.slides])


    const init = async () => {
        try {
            const slideArray = await sliderImages();
            setSlides(slideArray);
        } catch (err) {

        }
    }

    useEffect(() => {
        init();
    }, []);

    const maxSteps = slides && slides.length;


    const handleNext = () => {
        setActiveStep((prevActiveStep) => prevActiveStep + 1);
    };

    const handleBack = () => {
        setActiveStep((prevActiveStep) => prevActiveStep - 1);
    };

    const handleStepChange = (step) => {
        slides && setActiveStep(step);
    };


    console.log(slides && slides);

    return (
        <div className={classes.root}>
            {/* <Paper square elevation={0} className={classes.header}> */}
            {/* <Typography>{tutorialSteps[activeStep].label}</Typography> */}
            {/* </Paper> */}
            <AutoPlaySwipeableViews
                axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
                index={activeStep}
                onChangeIndex={handleStepChange}
                enableMouseEvents
            >
                {slides && slides.map((step, index) => (
                    <div key={step._id}>
                        {Math.abs(activeStep - index) <= 2 ? (
                            //   <img className={classes.img} src={step.imgPath} alt={step.label} />
                            <img
                                className={classes.img}
                                alt={step.title}
                                src={`${API}/slider/photo/${step._id}`}
                            />
                            // <ShowSlider item={step._id} url="" idenClass="left-slide" idenId="left-slide" />


                        ) : null}
                    </div>
                ))}
            </AutoPlaySwipeableViews>
            <MobileStepper
                steps={maxSteps}
                position="static"
                variant="dots"
                activeStep={activeStep}
                nextButton={
                    <Button size="small" onClick={handleNext} disabled={activeStep === maxSteps - 1}>
                        {/* Next */}
            {/* {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />} */}
                    </Button>
                }
                backButton={
                    <Button size="small" onClick={handleBack} disabled={activeStep === 0}>
                        {/* {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />} */}
            {/* Back */}
          </Button>
                }
            />
        </div>
    );
}

export default SwipeableTextMobileStepper;
